.writings {
  padding: 20px;
  font-family: 'OCR A Std', sans-serif !important; /* A modern, cyberpunk-style font */
  color: #333;
}

.content {
  max-width: 800px;
  margin: 0 auto;
}

h2 {
  text-align: center;
  color: #000;
  margin-bottom: 20px;
  font-size: 24px;
}

.writingsTable {
  width: 100%;
  background-color: #fbf9e8;
  border-radius: 10px;
  padding: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-collapse: collapse;
}

.writingsTable th,
.writingsTable td {
  padding: 10px 6px;
  text-align: left;
  vertical-align: top; /* Align all cells (except header) to the top */
}

.writingsTable th {
  background-color: #494949;
  outline: 2px solid #494949;
  color: white;
  font-weight: bold;
  font-size: 16px;
  vertical-align: middle; /* Ensure headers stay in the middle */
}

.writingsTable tbody tr {
  background-color: #ffffff;
  border-bottom: 1px solid #e0e0e0;
  transition: all 0.2s ease-in-out;
}

.writingsTable tbody tr:hover {
  background-color: #ffffff;
  outline: 2px solid #000000; /* Use outline instead of border */
}


.articleLink {
  text-decoration: none;
  color: #5859ac;
  font-weight: bold;
  transition: color 0.2s ease-in-out;
}

.articleLink:hover {
  color: #5390b9;
}

.articleLink:visited {
  color: #5859ac;
}

.writingsTable td:nth-child(3) {
  font-size: 12px;
  font-style: italic;
  color: #555;
}
