.contactbtn {
    border: 1px solid black;
    border-radius: 5px;
    padding: 10px 0; /* Increased padding for more height */
    font-weight: bold;
    cursor: pointer;
    background: none;
    text-decoration: none;
    color: inherit;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: ease-in-out 0.2s;
    width: 100%;
    font-size: 24px;
}

.socialLinks {
    display: flex;
    justify-content: space-between;
    gap: 15px;
    margin-bottom: 40px;
}

.contactbtn.x {
    color: black;
    border-color: black;
}

.contactbtn.x:hover {
    color: white;
    background-color: black;
}

.contactbtn.github {
    color: #333;
    border-color: #333;
}

.contactbtn.github:hover {
    color: white;
    background-color: #333;
}

.contactbtn.linkedin {
    color: #0077B5;
    border-color: #0077B5;
}

.contactbtn.linkedin:hover {
    color: white;
    background-color: #0077B5;
}

.contactbtn.medium {
    color: #000000;
    border-color: #000000;
}

.contactbtn.medium:hover {
    color: white;
    background-color: #000000;
}
