.work {
  padding: 20px;
  font-family: 'OCR A Std', sans-serif !important; /* Matching the cyberpunk-style font */
  color: #333;
}

.content {
  max-width: 800px;
  margin: 0 auto;
}

h2 {
  text-align: center;
  color: #000;
  margin-bottom: 20px;
  font-size: 24px;
}

.workTable {
  width: 100%;
  background-color: #fbf9e8;
  border-radius: 10px;
  padding: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-collapse: collapse;
}

.workTable th,
.workTable td {
  padding: 10px 6px;
  text-align: left;
  vertical-align: top;
}

.workTable th {
  background-color: #494949;
  outline: 2px solid #494949;
  color: white;
  font-weight: bold;
  font-size: 16px;
  vertical-align: middle;
}

.workTable tbody tr {
  background-color: #ffffff;
  border-bottom: 1px solid #e0e0e0;
  transition: all 0.2s ease-in-out;
}

.workTable tbody tr:hover {
  background-color: #ffffff;
  outline: 2px solid #000000;
}

.repoLink {
  text-decoration: none;
  color: #5859ac;
  font-weight: bold;
  transition: color 0.2s ease-in-out;
}

.repoLink:hover {
  color: #5390b9;
}

.repoLink:visited {
  color: #5859ac;
}

.workTable td:nth-child(3) {
  font-size: 12px;
  font-style: italic;
  color: #555;
}

.noProjects {
  text-align: center;
  color: #888;
  font-style: italic;
  padding: 20px;
}