.menu {
    background-color: white;
    padding: 30px 15% 0px 15%;
    display: flex; /* Use flexbox */
    justify-content: center; /* Center the navigation items */
    align-items: center;
}

.navbtn {
    color: black;
    margin: 0px 35px;
    text-decoration: none;
    font-weight: 400;
    transition: ease-in-out 0.2s;
    display: inline-block;
    flex-shrink: 0; /* Prevent shrinking */
    padding: 0 10px; /* Add padding to reserve space */
}

.navbtn:hover {
    color: black;
    font-weight: 900;
}

.menu > .navbtn {
    flex-grow: 0; /* Ensure no growth */
    flex-basis: auto; /* Keep size based on content */
}
